.social-list {
    display: flex;
    justify-content: space-between;
  

  
    &__item {
  
  
      &:not(:last-child) {
        margin-right: 13px;
      }
    }
  
    &__link {
      cursor: pointer;
      display: inline-flex;
      align-items: center;
      justify-content: center;
      width: 36px;
      height: 36px;
      transition: background-color 0.4s ease-in-out;
  
      svg path {
        transition: fill 0.4s ease-in-out;
      }

      &--vk{
      @include for-desktop {
        
          &:focus-visible {
            //   background-color: $primary_light;
      
              svg {
      
                path {
                  fill: $blue;
                }
              }
            }
      
            &:hover:not(:focus-visible) {
              svg path {
                fill: $blue;
              }
            }
        }

        &:active:not(:focus-visible) {
          svg path {
            fill: $blue_shade;
          }
        }
      }
  
      &--insta{
        @include for-desktop {
          
            &:focus-visible {
              //   background-color: $primary_light;
        
                svg {
        
                  path {
                    fill: $primary_light;
                  }
                }
              }
        
              &:hover:not(:focus-visible) {
                svg path {
                  fill: $primary_light;
                }
              }
          }
  
          &:active:not(:focus-visible) {
            svg path {
              fill: $primary;
            }
          }
        }
  
    }
  
  }