.pulse-button {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    width: 120px;
    height: 80px;
    border-radius: 20px;
    padding: 20px;
    border: none;
    background-color: $green;
    box-shadow: 0 5px 35px rgba(0, 0, 0, 0.2);
    position: fixed;
    bottom: 70px;
    right: 50px;
    cursor: pointer;
    outline: none;
    z-index: 1000;
    transition: background-color 0.3s ease-in-out;

    @include for-desktop{
      &:hover{
        background-color: $green_light;
      }
    }

    &__text {
    display: block;
    width: 100%;
    height: 30px;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    line-height: 1.2;
    position: absolute;
    margin-top: 1px;
    
  }

  &__link{
    font-family: 'Muller';
    font-size: 16px;
    line-height: 100%;
    color: $white;
    background-color: transparent;
  }

    &__rings {
      border: 1px solid $white;
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      height: auto;
      width: auto;
      border-radius: 20px;
      animation-duration: 2.0s;
      animation-name: pulse_1;
      animation-iteration-count: infinite;
      z-index: -1;

      &:nth-child(2) {
        animation-name: pulse_2;
      }
      &:nth-child(3) {
        animation-name: pulse_3;
      }

    }

  }


  @keyframes pulse_1 {
    from {
      opacity: 1;
      transform: scale(1, 1);
    }
   
    to {
      opacity: 0;
      transform: scale(1.2, 1.2);
    }
  }
   
  @keyframes pulse_2 {
    from {
      opacity: 1;
      transform: scale(1, 1);
    }
   
    to {
      opacity: 0;
      transform: scale(1.3, 1.3);
    }
  }
   
  @keyframes pulse_3 {
    from {
      opacity: 1;
      transform: scale(1, 1);
    }
   
    to {
      opacity: 0;
      transform: scale(1.4, 1.4);
    }
  }