.header{
    position: sticky;
    top: 0;
    z-index: 10;
    background-color: $grey_shade;

    &__container{
        display: flex;
        flex-direction: column;
        align-items: center;
        padding-top: 20px;
        padding-bottom: 40px;
        @include small-tablet{
            flex-direction: row;
            justify-content: space-between;
        }
        @include mobile{
          padding-bottom: 20px;
      }
    }
}

.burger {
    @include burger;
    display: none;
  
    @include small-tablet {
      display: block;
  
    }
  
    @include mobile {
      margin-right: 0;
    }
  }
  
  .logo {
    margin-bottom: 20px;
    @include small-tablet {
        margin-bottom: 0;
        position: absolute;
        top: 25px;
        left: 50%;
        transform: translateX(-50%);
    }
  
    @include mobile {

    }
  }

