.footer{
    &__container{
        padding-top: 20px;
        padding-bottom: 20px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        @include mobile{
            padding-top: 10px;
            padding-bottom: 10px;
            flex-direction: column;
        }
    }
    &__logo{
        @include mobile{
           padding-bottom: 10px;  
        }
    }

    &__call{
        font-family: 'Muller';
        font-size: 18px;
        line-height: 100%;
        color: $white;
        transition: transform 0.3s ease-in-out;

        @include for-desktop{
            &:hover{
                transform: scale(1.2);
            }
        }
        @include mobile{
            padding-bottom: 10px;  
         }
    }
    &__call-descr{
        margin-right: 10px;
    }
    &__call-link{
        font-family: 'Muller';
        font-size: 18px;
        line-height: 100%;
        color: $white;
        cursor: pointer;
    }
}