html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.mp-reset{
  margin: 0;
  padding: 0;
}




body{
  font-family: 'Open Sans';
  overflow-anchor: none;
  background-color: $grey_shade;
}

img {
  width: 100%;
  height: auto;
  object-fit: cover;
}

a {
  text-decoration: none;
}

*:focus-visible {
  outline: 0;
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 20px;
  max-width: 1320px;

  @include tablet{
  padding: 0 36px;
  }
  @include small-tablet{
    padding: 0 72px;
  }
  @include mobile{
    padding: 0 12px;
  }
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.grid{
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: 32px;
  @include tablet{
    grid-template-columns: repeat(9, 1fr);

  }
  @include small-tablet{
    grid-template-columns: repeat(6, 1fr);
  }
  @include mobile{
    grid-template-columns: repeat(6, 1fr);
  }

}

.logo{

}

.main-title  {
  display: none;

}

.section-title{
  width: 75%;
  margin-bottom: 26px;
  font-family: 'Open Sans';
  font-size: 32px;
  line-height: 100%;
  color: $white;

  @include mobile{
    font-size: 24px;
    line-height: 100%;
  }


}

.btn {
  display: inline-block;
  background-color: transparent;
  padding: 22px 50px;
  border-radius: 10px;
  font-family: 'Open Sans Bold';
  font-size: 16px;
  line-height: 100%;
  color: $white;
  transition: color 0.4s ease-in-out, background-color 0.4s ease-in-out,border-color 0.4s ease-in-out, transform 0.4s ease-in-out;

  &--stroke{

      padding: 16px 32px;
      background: transparent;
      border: 2px solid $white;
      border-radius: 10px;
      color: $white;
      cursor: pointer;
      transition: color 0.4s ease-in-out, background-color 0.4s ease-in-out,border-color 0.4s ease-in-out, transform 0.4s ease-in-out;



  }
  
  &--stroke-ef{
    transition: color 0.4s ease-in-out, background-color 0.4s ease-in-out,border-color 0.4s ease-in-out, transform 0.4s ease-in-out;

    &:hover:not(:focus-visible){
      background-color: $grey_light;
      color: $grey_shade;

    }
    &:focus-visible{
        background-color: $grey_light;
        color: $grey_shade;
    }
    &:active:not(:focus-visible){
        background-color: $white;
        border-color: $grey_light;
        color: $grey_shade;
    }
  }

}
