.nav{
    display: flex;
    align-items: center;
    justify-content:space-between;
    width: 100%;

    @include small-tablet {
        width: 100%;
        height: 100vh;
        position: absolute;
        top: 15px;
        left: 0;
        justify-content: flex-start;
        z-index: 99;
        transform: translateX(-100%);
        transition: transform 0.4s ease-in-out;
    
        &--active {
          transform: none;
          left: 0;
          background-color: $grey_shade;
        }
      }
    
      @include mobile {
        width: 100%;
        position: absolute;
        top: 20px;
        left: 0;
        z-index: 99;
        transform: translateX(-100%);
        transition: transform 0.4s ease-in-out;
    
        &--active {
          transform: none
        }
      }



    &__list{
        display: flex;
        align-items: center;
        justify-content:space-between;
        width: 100%;

        @include small-tablet {
            width: 56%;
            margin-top: 91px;
            padding-left: 72px;
            height: 100%;
            display: flex;
            flex-direction: column;
            flex-wrap: wrap;
            justify-content: flex-start;
            align-items: flex-start;
          }
      
          @include mobile {
            margin-top: 70px;
            padding-left: 14px;
            width: 56%;
            display: flex;
            flex-direction: column;
            flex-wrap: nowrap;
            align-items: flex-start;
          }
    }

    &__item{
        position: relative;
        transition: transform 0.3s ease-in-out;
        
        &:not(:last-child){
            margin-right: 10px;
            @include small-tablet{
                margin-right: 0;
                margin-bottom: 10px;
            }
        }
        @include for-desktop{
            &:hover{
                transform: scale(1.1);
                .sub-menu__list{
                    display: block;
            }
        }
        }
        &:nth-child(3){
            .arrow{
                &--open{
                    @include tablet{

                    }
                    @include small-tablet{
                        right: 140px;
                    }
                    
                    @include mobile{
                        right: 93px;
                    }
                }
            }
        }
    }

    &__link{
        font-family: 'Muller';
        font-size: 24px;
        line-height: 100%;
        color: $white;

        &--ef{
            &:hover{
                    
            }
        }
    }
    &__arrow{

    }
}

.arrow{
    display: none;
    position: absolute;
    right: -30px;
    top: 9px;
    width: 0;
    height: 0;
    border-top: 10px solid $white;
    border-right: 10px solid transparent;
    border-left: 10px solid transparent;
    transition: transform 0.3s ease-in-out;

    @include tablet{
        display: block;
    }
    &--open{
        right: -30px;
        transform: rotate(180deg);
        @include small-tablet{
            right: 40px;
            transform: rotate(180deg);
        }

    }
}
.sub-menu{
    &__list{
        display: none;
        min-width: 270px;
        padding-top: 10px;
        padding-left: 25px;
        padding-right: 15px;
        padding-bottom: 15px;
        border: 1px solid $white;
        background-color: $grey_shade;
        position: absolute;
        left: 0;
        top: 25px;
        border-radius: 10px;
        transition: display 0.3s ease-in-out;

        &--open{
            display: block;
        }

        @include small-tablet{
            border: none;
            position: relative;
            top: 0;
            left: 0;
        }

    }

    &__item{
        background-color: $grey_shade;
        padding-left: 10px;
        transition: transform 0.3s ease-in-out;
        &:not(:last-child){
            margin-bottom: 10px;
        }
        @include for-desktop{
            &:hover{
                transform: scale(1.1);
            }
    
        }

    }
    &__link{
        font-family: 'Muller';
        font-size: 16px;
        line-height: 100%;
        color: $white;

    }
}