.portfolio{

    &__container{
        padding-bottom: 40px;
        @include mobile{
            padding-bottom: 20px;
        }
    }

    &__item{
        @include for-desktop{
            &:hover{
                .photo::before{
                    opacity: 1;
                    visibility: visible;
                }

            }
        }

    }

    &__individual-item{
        grid-column: 3 span;
        @include mobile{
            grid-column: 6 span;  
        }
    }
    &__family-item{
        grid-column: 3 span;
        @include mobile{
            grid-column: 6 span;  
        }
    }
    &__lovestory-item{
        grid-column: 3 span;
        @include mobile{
            grid-column: 6 span;  
        }
    }
    &__wedding-item{
        grid-column: 3 span;
        @include mobile{
            grid-column: 6 span;  
        }
    }
    
    &__title{
        font-family: 'Muller';
        font-size: 20px;
        line-height: 100%;
        color: $white;
        margin-bottom: 20px;
    }
    &__wrapper {
        display: flex;
        flex-direction: column;
        align-items: center;
        padding-bottom: 20px;
      }

      &__btn{
        margin-top: 25px;
      }
    
}

.photo{
    position: relative;

    &::before{
        content: '';
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        opacity: 0;
        visibility: hidden;
        background: rgba(255,255,255, 0.5);
        background-image: url('../img/zoom.svg');
        background-repeat: no-repeat;
        background-position: center center;
        background-size: 80px;
        transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
        cursor: pointer;
        
    }

    &__btn{
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;

    }
}