.services{
    &__container{
        padding-bottom: 40px;
        @include mobile{
            padding-bottom: 20px;
        }
    }
    &__title{
        font-family: 'Muller';
        font-size: 20px;
        line-height: 100%;
        color: $white;
        margin-bottom: 20px;
    }
    &__sub-title{
        font-family: 'Muller';
        font-size: 18px;
        line-height: 100%;
        color: $white;
        margin-bottom: 10px;
    }

    &__card{
        grid-column: 4 span;
        @include tablet{
            grid-column: 3 span;
        }
        @include small-tablet{
            grid-column: 2 span;
        }
        @include mobile{
            grid-column: 6 span;
        }
    }

    &__list{
        margin-bottom: 20px;
    }
    &__list-item{
        font-family: 'Muller';
        font-size: 16px;
        line-height: 100%;
        color: $white;
        &:not(:last-child){
            margin-bottom: 10px;
        }
    }

}